.character_sheet__container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 10in;
  width: 7.5in;
  margin: 0 auto;
}

.character_sheet__background_text {
  color: lightgrey;
}

.character_sheet__hidden_text {
  color: white;
}

.character_sheet__header {
  border-bottom: 1px solid black;
}

.character_sheet__body {
  display: inline-flex;
  justify-content: space-between;
  align-items: flex-start;
}

.character_sheet__body__attributes {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.character_sheet__attribute_box__container {
  border: 1px solid black;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  margin-top: 1rem;
}

.character_sheet__attribute_box__title {
  border-bottom: 1px solid;
  width: 100%;
  display: flex;
  justify-content: center;
}
.character_sheet__attribute_box__title > h5 {
  align-self: center;
  margin: .25rem;
}

.character_sheet__attribute_box__stat {
  width: 100%;
  display: flex;
  justify-content: center;
}

.character_sheet__attribute_box__stat > h4 {
 align-self: center;
 margin: .5rem 0;
}

.character_sheet__body__content {
  display: flex;
  justify-content: space-between;
}

.character_sheet__body__group {
  display: flex;
  justify-content: space-between;
  width: 27%;
}

.character_sheet__body__group_attributes {
  width: 1.1in;
}

.character_sheet__body__group_wrap {
  flex-wrap: wrap;
}

.character_sheet__body__group__item {
  display: flex;
  border: 1px solid;
  flex-direction: column;
  margin-top: 1rem;
  padding: .25rem 1rem;
  width: 100%;
}

.character_sheet__body__resource_name {
  text-align: center;
}

.character_sheet__body__resource_name_additional {
  margin: .8rem 0;
}

.character_sheet__body__resource_name_secondary {
  margin: .5rem 0;
  text-align: center;
}

.character_sheet__body__group__item__stat {
  margin: .2rem;
}

.character_sheet__body__group__item__stat > h4 {
  border: 1px solid;
  padding: 1rem;
  margin: .5rem 0;
  text-align: center;
}

.character_sheet__body__group__item__levels > .character_sheet__body__group__item__levels__level {
  display: flex;
  justify-content: space-between;
  margin: .2rem 0;
}

.character_sheet__body__group__item__levels > .character_sheet__body__group__item__levels__level > h5, h6 {
  align-self: center;
}

.character_sheet__body__group__item__levels input[type='checkbox'] {
    -webkit-appearance:none;
    width:30px;
    height:30px;
    background:white;
    border-radius:5px;
    border:2px solid #555;
    margin: 0;
}

.character_sheet__body__group__item__levels input[type='text'] {
    -webkit-appearance:none;
    width:44px;
    height:24px;
    background:white;
    border-radius:2px;
    border:2px solid #555;
    margin-left: .5rem;
}

.character_sheet__body__primary_skill {

}

.character_sheet__body__secondary_skill {

}

.character_sheet__notes {
  flex-grow: 1;
  border: 1px solid;
  border-top: 0;
}

.paper {
  background: #fff;
  background: -webkit-gradient(linear, 0 0, 0 100%, from(#d9eaf3), color-stop(4%, #fff)) 0 4px;
  background: -webkit-linear-gradient(top, #d9eaf3 0%, #fff 8%) 0 4px;
  background: -webkit-gradient(linear, left top, left bottom, from(#d9eaf3), color-stop(8%, #fff)) 0 4px;
  background: linear-gradient(top, #d9eaf3 0%, #fff 8%) 0 4px;
  -ms-background-size: 100% 23px;
  background-size: 100% 23px;

  height: 100%;
}


.coreRules__Container {
    width: 80vw;
    margin: 0 10vw;
    display: inline-flex;
}

.sideBar {
    top: 2rem;
    position: -webkit-sticky;
    position: sticky;
    border: 1px solid black;
    margin: 2rem 2rem 2rem 0;
    min-width: calc(40vw - 20rem);
    height: 25rem;
    text-align: center;
}

.coreRules {
    max-width: 40rem;
}

.rules_list h4{
    margin-bottom: .3rem;
}
.rules_list h3{
    margin-bottom: .5rem;
}
.rules_list p {
    margin: .5rem 0 0;
}
.rules_list ul {
    margin: 0;
}

.traits_list {
    list-style-type: none;
    padding: 0;
}


.dataDisplay__container {
    width: 80vw;
    margin: auto;

}

.dataDisplay__title__container {
    border-bottom: 3px #660066 solid;
    padding: 2rem 0;
    height: 2rem;
}
.dataDisplay__title {
    float: left;
    margin: 0;
}



.dataDisplay__list__container sgv {
    min-width: 2rem;
}


.dataDisplay__list__container__2 .dataDisplay__list__content__container {
    display: flex;
}
.dataDisplay__list__container__2 .dataDisplay__list__content__container > div:first-of-type {
    min-width: 10rem;
    width: calc(40vw - 20rem);
}
.dataDisplay__list__container__2 .dataDisplay__list__content__container > div:nth-of-type(2) {
    border: 1px solid;
    width: calc(80% - 3rem);
    max-width: 40rem;
    padding: 1rem;
    margin: -1rem auto;
}
.dataDisplay__list__container__2 .dataDisplay__list__content__container > div:nth-of-type(2) .placeholderText {
    text-align: center;
    margin-top: 3rem;
}

.dataDisplay__list__container__2 .dataDisplay__list__content__container > div:nth-of-type(2) {
    margin-top: 0;
}
.dataDisplay__list__container__2 .dataDisplay__list__table__row > *:nth-of-type(2) {
    float: right;
    cursor: pointer;
    color: #660066;
}


/** TODO make these variable based on view width */
.dataDisplay__list__container__5 .dataDisplay__list__table__row svg:first-of-type {
    width: 25%;
}
.dataDisplay__list__container__5 .dataDisplay__list__table__row > *:first-of-type {
    width: 25%;
}
.dataDisplay__list__container__5 .dataDisplay__list__table__row > *:nth-of-type(2) {
    width: 20%;
}
.dataDisplay__list__container__5 .dataDisplay__list__table__row > *:nth-of-type(3) {
    width: 20%;
}
.dataDisplay__list__container__5 .dataDisplay__list__table__row > *:nth-of-type(4) {
    width: 30%;
}
.dataDisplay__list__container__5 .dataDisplay__list__table__row > *:nth-of-type(5) {
    width: 5%;
    cursor: pointer;
    color: #660066;
}

.dataDisplay__list__header {
    margin-left: 1rem;
}
.dataDisplay__list__header h5 {
    display: inline-block;
    padding: 1rem 0;
}
.dataDisplay__list__item {
    text-align: left;
    list-style: none;
    padding: .5rem;
    margin: .5rem;
    border: 1px solid #660066;
    border-radius: 20%;
    font-weight: bold;
}
.dataDisplay__list__item div {
    display: inline-flex;
    text-align: left;
}

.expandedListDisplay {
    border: 1px solid #660066;
    border-top: none;
    padding: 1rem 3rem;
    margin: -1rem .5rem 0;
}

.expandedListContent {
    max-width: 40rem;
    margin: 1rem auto 0;
}

.expandedListContent *:last-child {
    margin-bottom: 0;
    font-size: 1rem;
}


.dataDisplay__docLink {
    float: right;
    text-decoration: none;
    color: white;
    background-color: #1c1d1e;
    cursor: pointer;
    border-radius: 20%;
    margin: -1.5rem 0rem;
}

.dataDisplay__docLink * {
    margin: .2rem;
}

.dataDisplay__filter__container {
    display: inline-flex;
    background: darkgrey;
    width: 100%;
}
.dataDisplay__filter__container > div {
    margin: 1rem;
}
.dataDisplay__filter__container > div > h4,h5,h6 {
    margin: 0;
}

.dataDisplay__filter__name__input {
    height: 3rem;
    padding: 0 1rem;
    font-size: 1.5rem;
    margin-top: .5rem;
}

.dataDisplay__filter__button {
    background: #660066;;
    color: white;
    cursor: pointer;
    width: 8rem;
    text-align: center;
    border-radius: 20%;
    padding: 1rem;
}

.dataDisplay__filter__button * {
    margin: 0;
}

.resetFilters__button {
    padding: 1rem 0 0 1.5rem;
    color: #660066;
    cursor: pointer;
}

.sortIcon {
    max-width: 2rem;
    cursor: pointer;
}

.checkmark__container {
    display: inline-flex;
}

.checkmark__container > div {
    margin-right: 1rem;
}

.dataDisplay__filter__checkBox {
    margin: auto;
}

.header {
    background: -webkit-gradient(linear, left top, left bottom, from(#660066),  color-stop(#9900CB), to(#660066));
    background: -webkit-linear-gradient(top, #660066,  #9900CB, #660066);
    background: linear-gradient(180deg, #660066,  #9900CB, #660066);
    color: white;
    margin-top: 0;
    text-align: left;
    padding-left: 10vw;
    padding-right: 10vw;
}

.header__title {
    margin: 0;
    padding: .67rem 0;
}

@media print {
  .header {
    display: none;
  }
}

.topNav {
    background: #1c1d1e;
    color: white;
    margin-top: 0;
}

@media print {
  .topNav {
    display: none;
  }
}

.topNav__list {
    display: flex;
    list-style: none;
    margin: 0;
    padding: .3rem 10vw;
    font-size: 1.1rem;
    text-align: left;
}

.topNav__list li {
    width: 8.5rem;
    cursor: pointer;
    margin-right: 2rem;
}

.topNav__list a {
    text-decoration: none;
    color: white;
    padding-right: .5rem;
}

.extendedNav__container {
    background: #26282a;
    border-top: solid grey 1px;
    text-align: left;
    padding: 0 10vw;
}

.extendedNav__items__container {
    display: inline-flex;
    width: 100%;
}

.chapterLink {
    display: block;
    cursor: pointer;
    font-size: 1rem;
    margin-left: -.5rem;
    padding: .5rem;
    width: 10rem;
    border-radius: 20%;
    text-decoration: none;
    color: white;
    padding-right: .5rem;
}

.hoverableNav:hover {
    background-color: midnightblue;
    box-shadow: inset 0px 0px 5px #c1c1c1;
}

.chapterSectionsContainer {
    display: inline-flex;
    min-width: 21rem;
}

.rulesToolsContainer {
    max-width: 50rem;
    min-width: 25rem;
}

.toolButton {
    text-decoration: none;
    color: white;
    cursor: pointer;
    width: 30%;
    min-width: 10rem;
    display: inline-block;
    margin: 1%;
    text-align: center;
    border: solid 2px dimgray;
    border-radius: 20%
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.3rem;
}

p, li {
    font-size: 1rem;
    line-height: 1.3rem;
}

li > h3, h4 {
    font-size: 1.3rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hidden {
    display: none;
}

